/* === General Styles === */
body {
  font-family: 'Arial', sans-serif;
  background-color: #f8f9fa;
  color: #333;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

/* === Navbar Styles === */
.navbar {
  width: 100%;
  max-width: 1200px; /* Match hero-section */
  margin: auto; /* Center the navbar */
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 30px;
  background-color: #1e1e1e;
  border-radius: 10px; /* Match hero-section border radius */
}

.content {
  padding-top: 30px;
}

.logo-link {
  text-decoration: none;
  color: white;
  font-size: 30px;
  font-weight: bold;
  transition: color 0.3s;
}

.logo-link:hover {
  color: yellow;
}

.portfolio-nav {
  display: flex;
  gap: 20px;
}

.portfolio-nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
  margin: 0;
  padding: 0;
}

.portfolio-nav li {
  display: inline;
}

.portfolio-nav a {
  text-decoration: none;
  color: white;
  font-weight: bold;
  transition: color 0.3s ease-in-out;
}

.portfolio-nav a:hover {
  color: yellow;
}

/* === Hero Section === */
.hero-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #0056b3;
  padding: 50px;
  border-radius: 10px;
  margin-top: 20px;
  width: -webkit-fill-available;
}

.hero-content {
  color: white;
}

.hero-content p {
  font-size: 18px;
}

.hero-content h1 {
  font-size: 48px;
  font-weight: bold;
  margin: 5px 0;
}

.hero-content h2 {
  font-size: 24px;
  font-weight: bold;
}

/* === Buttons === */
.hero-buttons {
  display: flex;
  gap: 15px;
  margin-top: 20px;
}

.btn-primary, .btn-secondary {
  padding: 12px 20px;
  font-size: 16px;
  font-weight: bold;
  border: none;
  cursor: pointer;
  border-radius: 8px;
  text-decoration: none;
  display: inline-block;
  text-align: center;
}

.btn-primary {
  background: yellow;
  color: black;
  border: 2px solid black;
}

.btn-secondary {
  background: white;
  color: black;
  border: 2px solid black;
}

.btn-primary:hover, .btn-secondary:hover {
  opacity: 0.8;
}

/* === Contact Page === */
/* === Contact Page Styling === */
.contact-page {
  background-color: #0056b3;
  width: 100%;
  max-width: 1200px;
  margin: auto;
  padding: 20px;
  border-radius: 10px;
}

/* === Contact Content Wrapper === */
.contact-content {
  display: flex;
  gap: 50px;
  align-items: center;
  justify-content: center;
  width: 100%;
}

/* === Contact Info Box === */
.contact-box {
  background: rgba(0, 0, 0, 0.9);
  color: white;
  padding: 25px;
  border-radius: 12px;
  text-align: left;
  width: 100%;
  max-width: 350px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
}

.contact-box h2 {
  font-size: 22px;
  margin-bottom: 15px;
  font-weight: bold;
  text-align: center;
}

.contact-details {
  display: flex;
  align-items: center;
  gap: 12px;
  margin: 15px 0;
  padding-bottom: 10px;
  border-bottom: 1px dashed #888;
}

/* Remove the last separator */
.contact-details:last-child {
  border-bottom: none;
}

.contact-icon {
  font-size: 20px;
  color: yellow;
}

/* === Contact Form Box === */
.contact-form-box {
  background: white;
  padding: 50px;
  border-radius: 12px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
  text-align: center;
  width: 100%;
  max-width: 400px;
}

/* === Contact Form Fields === */
.contact-form-fields {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.input-group {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
}

.input-group input,
.input-group textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 16px;
  padding-left: 50px; /* Ensures placeholder doesn't overlap with icon */
}

/* === Input Icons === */
.input-icon {
  position: absolute;
  left: 15px; /* Adjusted to prevent placeholder overlap */
  top: 50%;
  transform: translateY(-50%);
  color: #888;
  font-size: 18px;
}

/* === Textarea Styling === */
.input-group textarea {
  height: 100px;
  resize: none;
  padding-top: 12px;
  padding-bottom: 12px;
}

/* === Placeholder Styling === */
.input-group input::placeholder,
.input-group textarea::placeholder {
  font-size: 16px; /* Adjust size */
  color: #999; /* Light gray color */
  opacity: 1; /* Ensure full visibility */
  transition: all 0.3s ease-in-out;
  font-family: 'Arial', sans-serif;
}

/* === Send Button === */
.contact-submit-btn {
  background-color: #1E3A8A;
  color: white;
  font-size: 18px;
  padding: 12px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.contact-submit-btn:hover {
  background-color: #153477;
}

/* === Status Message === */
.contact-status {
  margin-top: 10px;
  font-size: 14px;
  font-style: italic;
  color: #333;
}

/* === Contact Link === */
.contact-link {
  color: yellow;
  text-decoration: none;
  font-weight: bold;
}

.contact-link:hover {
  text-decoration: underline;
  color: #fdd835;
}

/* === Mobile Responsive Fix === */
@media (max-width: 768px) {
  .contact-content {
    flex-direction: column;
    align-items: center;
    gap: 30px;
  }

  .contact-box,
  .contact-form-box {
    width: 100%;
    max-width: 350px;
  }
}

/* === Mobile Responsive Fix === */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    text-align: center;
    padding: 10px;
  }

  .portfolio-nav ul {
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }

  .hero-section {
    flex-direction: column;
    text-align: center;
    padding: 30px;
  }

  .hero-image {
    margin-top: 20px;
  }

  .contact-container {
    width: 90%;
    padding: 20px;
  }
}

