/* === Projects === */
/* === Projects Page Background === */
.projects-page {
  font-family: "Poppins", sans-serif; /* Use a modern, professional font */
  background-color: #f4f6f9;
  padding: 20px;
}

/* === Projects Content Wrapper === */
.projects-content {
  display: flex;
  flex-direction: column; /* Stack projects vertically */
  align-items: center;
  gap: 20px;
}

/* Project Header (Title + Icon) */
.project-header {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 22px;
  font-weight: bold;
  color: #2c3e50;
  border-bottom: 2px solid #3498db;
  padding-bottom: 8px;
  margin-bottom: 12px;
}

.project-header h3 {
  margin: 0;
}

/* Project Description */
.project-description {
  font-size: 16px;
  color: #34495e;
  line-height: 1.6;
  margin-bottom: 15px;
}

/* Features List */
.project-features {
  background: #ecf0f1;
  padding: 10px 15px;
  border-radius: 8px;
  font-size: 15px;
}

.project-features li {
  margin-bottom: 6px;
  color: #2c3e50;
}

/* Technologies Section */
.project-tech {
  font-size: 15px;
  font-weight: 600;
  color: #2980b9;
  margin-top: 12px;
}

/* Individual Project Cards */
.project-box {
  width: 80%;
  max-width: 700px;
  background: white;
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
}

.project-box:hover {
  transform: translateY(-5px); /* Slight hover effect */
}

/* === Project Titles === */
.project-box h3 {
  font-size: 22px;
  font-weight: bold;
  color: #1E3A8A;
}

.project-description p {
  margin-bottom: 10px;
}

/* === Project Description === */
.project-box p {
  font-size: 16px;
  color: #333;
}

/* === Project Tech Stack === */
.project-tech {
  font-size: 14px;
  font-weight: bold;
  color: #555;
  margin-top: 10px;
}

/* === Project Icons === */
.project-icon {
  width: 32px;
  height: 32px;
  color: #1E3A8A;
  margin-bottom: 10px;
}

/* === Responsive Design === */
@media (max-width: 768px) {
  .projects-content {
    flex-direction: column;
    align-items: center;
  }

  .project-box {
    max-width: 300px;
  }
}

.project-features {
  padding-left: 20px;
  margin-top: 10px;
}

.project-features li {
  margin-bottom: 5px;
}

.youtube-container {
  margin-top: 15px;
}

.project-images {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); /* Responsive grid */
  gap: 10px;
  margin-top: 15px;
}

.project-image {
  width: 100%;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
}

.project-image:hover {
  transform: scale(1.05); /* Slight zoom effect on hover */
}